<template>
    <div>
        <el-button @click.prevent="musicAdd" style="margin:20px 0;background-color:#169bd5;color:#fff"></el-button>
        <div class="home_table">
            <el-table :data="tableData" border style="width: 100%;">
                <template v-for="(item, index) in tableData">
                    <el-table-column :prop="item.prop" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true"></el-table-column>
                </template>
                <el-table-column fixed="right" label="" width="200">
                    <template slot-scope="scope">
                        <el-button @click="musicPre(scope.row)" type="text" size="small"></el-button>
                    </template>
                </el-table-column>
                <el-table-column fixed="right" label="" width="200">
                    <template slot-scope="scope">
                        <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
                        <el-button @click="delMusic(scope.row)" type="text" size="small"></el-button>
                    </template>
                </el-table-column>

            </el-table>
        </div>
        <!--  -->
        <div class="pagination">
            <el-pagination background :current-page.sync="page" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="prev, pager, next, sizes,total" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
        </div>
        <!--  -->
        <el-dialog title="" :visible.sync="centerDialogVisibleEit" width="30%">
            <!-- <span></span> -->
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="" prop="region">
                    <el-select v-model="ruleForm.region" multiple filterable allow-create default-first-option placeholder="">
                        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="">
                    <el-input v-model="ruleForm.name" style="width:auto;"></el-input>
                    <el-button size="mini" style="border:0;vertical-align: middle;">
                        <i class="el-icon-circle-close" style="color: red;font-size:xx-large"></i>
                    </el-button>
                    <span></span>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer" style="text-align:right">
                <el-button @click="handleCloseEsc"> </el-button>
                <el-button type="primary" @click="removeDomainDelEit"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="centerDialogVisible" width="30%" center>
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="centerDialogVisible = false"> </el-button>
                <el-button type="primary" @click="removeDomainDel"> </el-button>
            </span>
        </el-dialog>
    </div>

</template>

<script>
export default {
    name: "ringtoneConfiguration",
    data() {
        return {
            //
            tableData: [],

            //
            centerDialogVisible: false,
            centerDialogVisibleEit: false,
            ruleForm: {
                name: '',
                region: '',
            },
            rules: {
                name: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
                ],
                region: [
                    { required: true, message: '', trigger: 'change' }
                ],
            },

            options: [{
                value: 'HTML',
                label: 'HTML'
            }, {
                value: 'CSS',
                label: 'CSS'
            }, {
                value: 'JavaScript',
                label: 'JavaScript'
            }],
            value: []
        }
    },
    methods: {
        //
        musicAdd() {
            this.$router.push({ name: 'earlyWarningLightAdd' })
        },

        //
        handleClick() {
            this.centerDialogVisibleEit = true
        },
        //
        removeDomainDelEit() {
            this.centerDialogVisibleEit = false
        },
        //
        handleCloseEsc() {
            // console.log("");
            this.centerDialogVisibleEit = false,
                this.$refs.ruleForm.resetFields()
        },

        //
        delMusic() {
            this.centerDialogVisible = true
        },
        //
        removeDomainDel() {
            this.centerDialogVisible = false
            //
            console.log("");

        }
    }

}
</script>

<style lang="scss" scoped>
</style>


